import { useState } from "react";
import Button from "../Button";
import Keyboard from "../Keyboard";
import "./index.css";
import TopComponent from "../TopComponent";
import { withFocusable } from "@noriginmedia/react-spatial-navigation";

function InputContainer(props) {
    const [easyMode, setEasyMode] = useState(localStorage.easyMode === "true" ? true : false);

    const onClickNewGame = () => {
        document.dispatchEvent(new Event("NEW_GAME"));
    };
    const onClickMode = () => {
        localStorage.easyMode = !easyMode;
        setEasyMode(!easyMode);
    };

    return (
        <div className="InputContainer">
            <TopComponent
                easyMode={easyMode}
                onClickMode={onClickMode}
                onClickHowToButton={props.onClickHowToButton}
                onClickNewGame={onClickNewGame}
                toggleTheme={props.toggleTheme}
            ></TopComponent>

            <Keyboard focusKey={"Keyboard"} easyMode={easyMode}></Keyboard>
        </div>
    );
}

export default withFocusable()(InputContainer);
