import React from "react";
import "./index.css";

// const FILENAME = "TryRow";

class TryRow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <div className="TryRow">
                <span className="index">{this.props.index + 1 + "."}</span>
                <div className="wordContainer">
                    {[...Array(10)].map((ar, i) => {
                        return <span className="word">{this.props.word[i]}</span>;
                    })}
                    {/* <span className="word">{this.props.word}</span>
                    <span className="underlines">
                        <span className="underline">_</span>
                        <span className="underline">_</span>
                        <span className="underline">_</span>
                        <span className="underline">_</span>
                    </span> */}
                </div>

                <span className="tryResult">
                    {this.props.matchesData && (
                        <span>
                            {this.props.matchesData.bulls + "B"} {this.props.matchesData.cows + "C"}
                        </span>
                    )}
                </span>
            </div>
        );
    }
}

export default TryRow;
