import React from "react";
import TryRow from "../TryRow";
import "./index.css";
// import words from "../../words.json";

const NO_OF_TRIES = 10;

const FILENAME = "ResultsContainer";

class ResultsContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentTry: 0,
            currentChar: 0,
            resultData: Array(NO_OF_TRIES).fill(""),
            matchesData: [],
            won: false,
        };

        this.handleEnter = this.handleEnter.bind(this);
        this.handleKeychar = this.handleKeychar.bind(this);
        this.handleDelete = this.handleDelete.bind(this);
        this.processWord = this.processWord.bind(this);
        this.resetState = this.resetState.bind(this);

        // this.resetState();
    }

    componentDidMount() {
        document.addEventListener("ENTER", this.handleEnter);
        document.addEventListener("KEYBOARD_CHAR", this.handleKeychar);
        document.addEventListener("DELETE", this.handleDelete);
        document.addEventListener("NEW_GAME", this.resetState);
    }

    componentDidUpdate() {
        if (this.props.words && this.props.words.length > 0 && !this.word) {
            this.fetchNewWord();
        }
    }

    fetchNewWord() {
        this.word = this.props.words[this.randomIntFromInterval(0, this.props.words.length - 1)].toLowerCase();
    }

    randomIntFromInterval(min, max) {
        return Math.floor(Math.random() * (max - min + 1) + min);
    }

    resetState() {
        console.log(FILENAME, "resetState");
        this.fetchNewWord();
        this.setState({
            currentTry: 0,
            currentChar: 0,
            resultData: Array(NO_OF_TRIES).fill(""),
            matchesData: [],
            won: false,
        });

        console.log(FILENAME, "this.word", this.word);
    }

    handleEnter() {
        console.log(FILENAME, "handleEnter", this.state.currentTry, this.state.currentChar, this.state.resultData);
        if (this.state.currentChar === 4) {
            this.processWord();
            this.setState({
                currentChar: 0,
                currentTry: this.state.currentTry + 1,
            });
        }
    }
    handleKeychar(e) {
        console.log(FILENAME, "handleKeychar", e.detail, this.state.currentChar);
        if (this.state.currentChar < 4 && !this.state.resultData[this.state.currentTry].includes(e.detail)) {
            const newResultdata = [...this.state.resultData];
            newResultdata[this.state.currentTry] += e.detail;
            this.setState({
                resultData: newResultdata,
                currentChar: this.state.currentChar + 1,
            });
        }
    }
    handleDelete() {
        console.log(FILENAME, "handleDelete");
        if (this.state.currentChar > 0) {
            const newResultdata = [...this.state.resultData];
            newResultdata[this.state.currentTry] = newResultdata[this.state.currentTry].substring(
                0,
                newResultdata[this.state.currentTry].length - 1
            );
            this.setState({
                resultData: newResultdata,
                currentChar: this.state.currentChar - 1,
            });
        }
    }

    processWord() {
        console.log(FILENAME, "processWord", this.word, this.state.resultData[this.state.currentTry]);
        const currentTry = this.state.resultData[this.state.currentTry].toLowerCase();
        let bulls = 0;
        let cows = 0;

        for (let i = 0; i < 4; i++) {
            if (this.word.includes(currentTry[i])) {
                if (this.word[i].toLowerCase() === currentTry[i].toLowerCase()) {
                    bulls++;
                } else {
                    cows++;
                }
            }
        }

        if (bulls === 0 && cows === 0) {
            document.dispatchEvent(new CustomEvent("NO_MATCH", { detail: currentTry }));
        } else {
            document.dispatchEvent(new CustomEvent("SOME_MATCH", { detail: currentTry }));
        }
        const newMatchesData = [...this.state.matchesData];
        newMatchesData[this.state.currentTry] = {
            bulls: bulls,
            cows: cows,
        };
        this.setState({
            matchesData: newMatchesData,
            won: bulls === 4,
        });
        if (bulls === 4) {
            document.dispatchEvent(new CustomEvent("WIN"));
        }
    }

    render() {
        console.log(FILENAME, "render", this.state.resultData, this.state.currentTry, this.state.currentChar);
        const triesLeft = NO_OF_TRIES - this.state.currentTry;
        return (
            <div className="ResultsContainer">
                {this.state.resultData.map((word, i) => {
                    console.log(FILENAME, "resultData.map");
                    return <TryRow key={i} word={word} matchesData={this.state.matchesData[i]} index={i} />;
                })}
                {this.state.won && this.props.words != null && <span className="result">You have WON!</span>}
                {!this.state.won &&
                    this.props.words != null &&
                    (triesLeft > 0 ? (
                        <span className="result">You have {NO_OF_TRIES - this.state.currentTry} tries left</span>
                    ) : (
                        <span className="result">Word was {this.word}! Game Over!</span>
                    ))}
                {this.props.words === null && <span className="result">Error receiving word</span>}
            </div>
        );
    }
}

export default ResultsContainer;
