import "./App.css";
import InputContainer from "./components/InputContainer";
import ResultsContainer from "./components/ResultsContainer";
import { initNavigation } from "@noriginmedia/react-spatial-navigation";
import { withFocusable } from "@noriginmedia/react-spatial-navigation";
import firebase from "firebase/app";
import "firebase/firestore";
import { useEffect, useState } from "react";
import HowToContainer from "./components/HowToContainer";
import Confetti from "react-confetti";

// Required for side-effects
// require("firebase/firestore");

// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
const firebaseConfig = {
    apiKey: "AIzaSyCunI9-wjYUwjYjY6d5XJdYvS5bpVPtptE",
    authDomain: "cows-and-bulls-3da30.firebaseapp.com",
    projectId: "cows-and-bulls-3da30",
    storageBucket: "cows-and-bulls-3da30.appspot.com",
    messagingSenderId: "957661796079",
    appId: "1:957661796079:web:21a5dd4ecb224362b36d1a",
};

// Initialize Firebase
const app = firebase.initializeApp(firebaseConfig);

const db = firebase.firestore(app);

function App(props) {
    initNavigation();

    const [words, setWords] = useState([]);
    const [darkMode, setDarkMode] = useState(localStorage.darkMode === "true" ? true : false);
    const [showHowTo, setShowHowTo] = useState(localStorage.howToShown ? false : true);
    const [showConfetti, setShowConfetti] = useState(false);

    useEffect(() => {
        document.addEventListener("WIN", () => setShowConfetti(true));
        document.addEventListener("NEW_GAME", () => setShowConfetti(false));
    }, []);

    useEffect(() => {
        db.collection("cows-and-bulls-words")
            .get()
            .then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    console.log("WORDS_RECEIVED", doc.data().words);
                    // document.dispatchEvent(new CustomEvent("WORDS_RECEIVED", { detail: doc.data() }));
                    setWords(doc.data().words);
                    // setWords(null);
                });
            })
            .catch((error) => {
                console.log("getWords Error ", error);
                setWords(null);
            });
    }, []);

    const onClickHowToButton = () => {
        setShowHowTo(!showHowTo);
    };
    const toggleTheme = () => {
        localStorage.darkMode = !darkMode;
        setDarkMode(!darkMode);
    };
    const onClickHowToContainer = () => {
        if (showHowTo) {
            setShowHowTo(false);
            props.setFocus("Keyboard");
        }
    };

    return (
        <div className={"App" + (darkMode ? " dark" : " light")}>
            {showConfetti && <Confetti></Confetti>}
            {showHowTo && (
                <HowToContainer
                    onEnterPress={onClickHowToContainer}
                    onClick={onClickHowToContainer}
                    focusKey={"HowToContainer"}
                ></HowToContainer>
            )}
            <InputContainer
                focusKey={"Input Component"}
                onClickHowToButton={onClickHowToButton}
                toggleTheme={toggleTheme}
            ></InputContainer>
            <ResultsContainer words={words}></ResultsContainer>
        </div>
    );
}

export default withFocusable()(App);
