import "./index.css";
import { withFocusable } from "@noriginmedia/react-spatial-navigation";
function Keys(props) {
    const className =
        "Keys" +
        (props.focused ? " focused" : "") +
        (props.noMatch ? " noMatch" : "") +
        (props.someMatch ? " someMatch" : "");
    return (
        <span className={className} onClick={props.onClick} data-char={props.char}>
            {props.specialKeysMapping[props.char] ? props.specialKeysMapping[props.char] : props.char}
        </span>
    );
}

export default withFocusable()(Keys);
