import { withFocusable } from "@noriginmedia/react-spatial-navigation";
import "./index.css";

function Button(props) {
    return (
        <button {...props} className={"Button " + props.className + (props.focused ? " focused" : "")}>
            {props.children}
        </button>
    );
}

export default withFocusable()(Button);
