import Keys from "./Keys";
import "./index.css";
import layout from "./layout.json";
import { withFocusable } from "@noriginmedia/react-spatial-navigation";
import React from "react";

const defaultLayout = window.innerWidth < 767 ? "qwerty-responsive" : "qwerty";

// const specialKeysMapping = { enter: "", delete: "" };
const specialKeysMapping = { enter: "CHECK", delete: "DEL" };

class Keyboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            keyboardLayout: props.layout ? props.layout : defaultLayout,
            noMatchChars: [],
            someMatchChars: [],
        };
        this.onClick = this.onClick.bind(this);
        this.onEnterPress = this.onEnterPress.bind(this);
        this.noMatch = this.noMatch.bind(this);
        this.someMatch = this.someMatch.bind(this);
        this.resetState = this.resetState.bind(this);
    }

    componentDidMount() {
        if (undefined !== localStorage.howToShown) {
            this.props.setFocus();
        }
        document.addEventListener("NO_MATCH", this.noMatch);
        document.addEventListener("SOME_MATCH", this.someMatch);
        document.addEventListener("NEW_GAME", this.resetState);
    }

    componentWillUnmount() {
        document.removeEventListener("NO_MATCH", this.noMatch);
        document.removeEventListener("SOME_MATCH", this.someMatch);
        document.removeEventListener("NEW_GAME", this.resetState);
    }
    resetState() {
        this.setState({
            noMatchChars: [],
            someMatchChars: [],
        });
    }
    noMatch(e) {
        console.log("nomatch", e.detail, this.state.noMatchChars);
        const newNoMatchChars = [...this.state.noMatchChars];
        for (let i = 0; i < 4; i++) {
            newNoMatchChars.push(e.detail[i]);
        }
        this.setState({
            noMatchChars: newNoMatchChars,
        });
    }
    someMatch(e) {
        console.log("somematch", e.detail, this.state.someMatchChars);
        const newSomeMatchChars = [...this.state.someMatchChars];
        for (let i = 0; i < 4; i++) {
            newSomeMatchChars.push(e.detail[i]);
        }
        this.setState({
            someMatchChars: newSomeMatchChars,
        });
    }

    callAppropriateFn(char) {
        if (char === "enter") {
            document.dispatchEvent(new Event("ENTER"));
            // props.enter();
        } else if (char === "delete") {
            // props.deleteChar();
            document.dispatchEvent(new Event("DELETE"));
        } else {
            // props.charClicked(char);
            document.dispatchEvent(new CustomEvent("KEYBOARD_CHAR", { detail: char }));
        }
    }
    onClick(e) {
        this.callAppropriateFn(e.target.dataset.char);
    }
    onEnterPress(e) {
        this.callAppropriateFn(e.char);
    }
    render() {
        return (
            <div className="Keyboard">
                {layout[this.state.keyboardLayout].map((row) => {
                    return (
                        <div key={row} className="keyboardRow">
                            {row.map((char) => {
                                return (
                                    <Keys
                                        noMatch={this.props.easyMode && this.state.noMatchChars.includes(char)}
                                        someMatch={this.props.easyMode && this.state.someMatchChars.includes(char)}
                                        focusKey={char}
                                        key={char}
                                        specialKeysMapping={specialKeysMapping}
                                        char={char}
                                        onClick={this.onClick}
                                        onEnterPress={this.onEnterPress}
                                    ></Keys>
                                );
                            })}
                        </div>
                    );
                })}
            </div>
        );
    }
}

export default withFocusable()(Keyboard);
